.festivalDates {
    font-size: 1.25rem;
    color: white;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: bold;
}

.unicorn {
    position: absolute;
    height: 150px;
    transform: translateX(-145px);
}

.unicorn.unicornMobile {
    height: 15rem;
    animation: moveUnicorn 4s infinite linear;
}

.landingPageTop {
    margin-bottom: 8rem;
}

.romoversikt {
    padding: 0 1rem;
}

.romfordelingHeading {
    margin: 0 auto;
    display: flex;
    margin-bottom: 3rem;
}

.etasje {
    margin-bottom: 10rem;
    width: 100%;
}

.menuBlocks {
    width: 50%;
    margin: 0 auto;
    padding: 5rem;
    display: flex;
    flex-direction: column;
    font-family: 'Source Sans Pro';
    font-weight: bold;
}

.menuBlock {
    height: 250px;
    width: 300px;
    font-size: 2rem;
    color: white;
    cursor: pointer;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .2s ease-in-out;
}

.menuBlock:hover {
    transform: scale(1.05);
}

.houseOverview {
    background-color: #FFCCD8;
}

.pakkeListe {
    background-color: #FFEED3;
}

.practicalInfo {
    background-color: #D1F9F2;
}

.menuBlock:nth-child(2) {
    align-self: flex-end;
    position: relative;
    top: -50px;
}

.menuBlock:nth-child(3) {
    position: relative;
    top: -50px;
}

@keyframes moveUnicorn {
    0%,
    100% {
        transform: translateX(10px) rotate(0deg);
    }
    10%,
    90% {
        transform: translateX(20px) rotate(5deg);
    }
    20%,
    80% {
        transform: translateX(30px) rotate(10deg);
    }
    30%,
    70% {
        transform: translateX(40px) rotate(15deg);
    }
    40%,
    60% {
        transform: translateX(50px) rotate(20deg);
    }
    50% {
        transform: translateX(60px) rotate(25deg);
    }
}

@media screen and (min-width: 400px) {
    .festival-dates {
        font-size: 1.5rem;
        letter-spacing: 2px;
    }

    .romoversikt {
        padding: 0 2rem;
    }
}

@media screen and (min-width: 770px) {
    .festivalDates {
        font-size: 2rem;
        color: white;
        text-transform: uppercase;
        letter-spacing: 3px;
        font-weight: bold;
    }

    .appHeader {
        padding: 40px;
    }

    .appHeader .spinningUser {
        height: 60px;
    }

    .romoversikt {
        padding: 0 3rem;
    }

    .romfordelingHeading {
        width: 80%;
    }

    .etasje {
        width: 60%;
    }
}

@media (prefers-reduced-motion: no-preference) {
    .spinningUser {
        animation: App-logo-spin infinite 3s linear;
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
