.taMedContainer, .ikkeTaMedContainer {
  width: 500px;
}

.taMed {
  border-right: 1px solid white;
  text-align: right;
}

.ikkeTaMed {
  text-align: left;
}

.taMed, .ikkeTaMed {
  padding: 0 2rem;
  font-weight: 200;
}

.taMed > *, .ikkeTaMed > *{
  margin-bottom: 20px;
}

.pakkeliste {
  width: 60%;
  margin: 0 auto;
  display: flex;

  flex-direction: row;
  justify-content: center;

  color: white;
  font-size: 2rem
}

.taMedContainer .pakkelisteHeading {
  text-align: right;
}

.ikkeTaMedContainer .pakkelisteHeading {
  text-align: left;
}

.pakkelisteHeading {
  margin-bottom: 1rem;
  padding-right: 2rem;
  padding-left: 2rem;
  color: #F36F8E;
}
